@keyframes fadeOut {
  from {
    opacity: 1; }
  50% {
    opacity: .5; }
  to {
    opacity: 0; } }

@keyframes fadeIn {
  from {
    opacity: 0; }
  50% {
    opacity: .5; }
  to {
    opacity: 1; } }

body {
  font: normal 1rem "Roboto", sans-serif;
  line-height: 1.5;
  color: #444; }

.topbar {
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3); }

.form-control {
  border-radius: 35px; }

.navbar {
  background: #ffffff;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3);
  padding: 10px 15px; }
  .navbar .nav-link,
  .navbar .dropdown-item {
    text-transform: uppercase;
    color: #000 !important;
    font-weight: 700;
    transition: ease-in-out .2s all; }
    .navbar .nav-link:hover,
    .navbar .dropdown-item:hover {
      color: #0E2B6E !important; }
  .navbar .dropdown-toggle::after {
    display: none; }
  .navbar .nav-link:focus,
  .navbar a:focus,
  .navbar .dropdown-item:focus {
    box-shadow: none; }
  .navbar .search-form .form-control,
  .navbar .search-form .btn {
    border-radius: 35px; }
  .navbar .search-form .form-control {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0; }
    .navbar .search-form .form-control:focus {
      border-color: #0E2B6E;
      box-shadow: none; }
  .navbar .search-form .btn {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border: 0;
    background-color: #0E2B6E; }
    .navbar .search-form .btn:hover {
      opacity: .8; }

.slider__news {
  height: 700px; }
  .slider__news-item {
    height: 700px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat; }
    .slider__news-item:after {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      z-index: 3;
      background: linear-gradient(to top, rgba(14, 43, 110, 0.9), rgba(14, 43, 110, 0.38), rgba(0, 0, 0, 0.2)); }
  .slider__news .owl-nav {
    position: absolute;
    z-index: 10;
    width: 100%;
    top: 50%;
    transform: translateY(50%);
    color: #ffffff;
    font-size: 30px; }
    .slider__news .owl-nav button {
      opacity: .7;
      transition: ease-in-out .2s all; }
      .slider__news .owl-nav button:hover {
        opacity: 1; }
    .slider__news .owl-nav .owl-prev {
      position: relative;
      left: 40px; }
    .slider__news .owl-nav .owl-next {
      position: absolute;
      right: 40px; }

.slider .item__description {
  position: absolute;
  z-index: 5;
  bottom: 40px;
  left: 40px;
  right: 40px;
  color: #fff; }
  .slider .item__description-category {
    margin-bottom: 10px;
    font-size: 0.875rem;
    font-style: italic;
    opacity: .5; }
  .slider .item__description-title {
    margin-bottom: 10px;
    font-size: 1.5rem;
    font-weight: 900; }
  .slider .item__description-text {
    margin-bottom: 30px;
    font-size: 1rem;
    font-weight: 300; }
  .slider .item__description .btn {
    padding-left: 35px;
    padding-right: 35px;
    border-radius: 35px;
    font-weight: 700;
    color: #000000;
    background-color: #ffffff;
    border: 0;
    box-shadow: 0 3px 15px rgba(255, 255, 255, 0.5);
    transition: ease-in-out .2s all; }
    .slider .item__description .btn:hover {
      box-shadow: none;
      color: #ffffff;
      background-color: #0E2B6E; }

@media only screen and (max-width: 840px) {
  .slider__news {
    height: 100vh; }
    .slider__news-item {
      height: 100vh; } }

.footer {
  background-color: #444444;
  color: #ffffff;
  padding: 50px 100px; }
  .footer .copyright {
    padding-left: 150px;
    color: #bcbbbb;
    font-size: .65rem; }
    .footer .copyright p {
      margin-bottom: 10px; }
    .footer .copyright p:last-child {
      margin-bottom: 0; }
  .footer .menu-list .menu-list-title {
    font-size: .9375rem;
    font-weight: bold; }
  .footer .menu-list .menu-item-arrow img {
    width: 16px; }
  .footer .menu-list .menu-item-link {
    font-size: .85rem; }
    .footer .menu-list .menu-item-link a {
      color: #ffffff; }
